
.caption {
  text-align: center;
  height: 100vh;
  display: flex;
  place-content: center;
  align-items: center;
  background: url("https://images.unsplash.com/photo-1530103862676-de8c9debad1d?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8MXx8fGVufDB8fHx8&w=1000&q=80") no-repeat;
  background-size: cover;
  color: rgb(0, 0, 0)02);
}
.card {
  backdrop-filter: blur(8px) saturate(180%);
  -webkit-backdrop-filter: blur(8px) saturate(180%);
  background-color: rgba(255, 255, 255, 0.39);
  border-radius: 12px;
  border: 1px solid rgba(209, 213, 219, 0.3);
}
